//TODO: ==========>>>> TYPOGRAPHY
a {
    text-decoration: none;
    color: inherit;
}

li {
    list-style: none;
    color: inherit;
}
h1 {
    font-size: rem(16);
    font-weight: $weight-300;

    @include media-query (tablet) {
        font-size: rem(22);
    }
}

h2 {
    font-size: rem(17);
    font-weight: $weight-200;

    @include media-query (tablet) {
        font-size: rem(18);
    }
}

h3 {
    font-size: rem(17);
    font-weight: $weight-200;

    @include media-query (tablet) {
        font-size: rem(18);
    }
}